.gameField_container {
	padding: 10px;

	background-color: blue;
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 10px;
}

.container {
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.gameResult {
	font-size: 30px;
}
